@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lilita+One&family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lilita+One&display=swap);
/* Google fonts to import */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body{
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  background-color: gainsboro;
  position: absolute;
}

.header{
  height: 16vh;
  min-height: 5em;
  width: 100%;
  position: fixed;
  display: flex;
  align-items: flex-start;
}

.strip{
  position: relative;
  z-index: 4;
  background-color: #382F2A;
  color: white;
  height: 55%;
  width: 100%;
  display: flex;
  padding-left: 12vw;
  justify-content: space-evenly;
  align-items: center;
  overflow: hidden;
}

.head-item{
  padding: 1.5em;
  font-family: 'Montserrat', sans-serif;
  font-weight: medium;
  font-size: x-large;
}

.head-item:hover{
  background-color: #564C45;
  transition: ease-in-out 0.2s;
  cursor: pointer;
}

.active:hover{
  background-color: gainsboro;
  color: #564C45;
}

.active{
  background-color: gainsboro;
  color: #564C45;
}

.tri{
  position: absolute;
  height: 100%;
  width: 16%;
  background-color: #595959;
  -webkit-clip-path: polygon(0% 0%, 40% 0%, 40% 100%, 0% 100%);
          clip-path: polygon(0% 0%, 40% 0%, 40% 100%, 0% 100%);
  z-index: 3;
}

.badge{
  position: absolute;
  z-index: 5;
  height: 100%;
  width: 16vh;
  min-width: 5em;
  background-color: white;
  left: 1.5vw;
  top: 3vh;
  border-radius: 100%;
  border-style: double;
  border-width: 1vh;
  border-color: #382F2A;
  box-shadow: #382F2A 0 0 0 8px;
}

.badge img{
  height: 100%;
  width: 100%;
  -webkit-transform: translateY(-5%);
          transform: translateY(-5%);
}

@media screen and (max-aspect-ratio: 10/7) {
  .strip{
    padding-left: 22vw;
  }

  .head-item{
    font-size: large;
  }
}

@media screen and (max-width: 600px) {
  .tri{
    display: none;
  }
  
  .toggledheader{
    margin: 0;
    height: 100vh;
    position: fixed;
    background-color: rgba(0,0,0,0.6);
    align-items: flex-start;
    justify-content: flex-end;
  }

  .badge{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    top: 1.5vh;
    left: 3vw;
  }
  
  .strip{
    background-color: rgba(0,0,0,0);
    padding-left: 0;
    visibility: hidden;
  }

  .invisible{
    visibility: hidden;
  }

  .toggledstrip{
    display: flex;
    width: 60vw;
    flex-direction: column;
    align-items: flex-start;
  }

  .head-item{
    font-size: x-large;
    width: 100%;
  }

  .badge:hover{
    cursor: pointer;
    -webkit-animation: radiate 3.5s linear;
            animation: radiate 3.5s linear;
  }

  .toggledbadge{
    border-color: #595959;
    box-shadow: #595959 0 0 0 8px;
  }

  @-webkit-keyframes radiate{
    4%{
      border-style: double;
      border-color: gainsboro;
      box-shadow: #382F2A 0 0 0 4px;
    }
    6%{
      border-style: solid;
      border-color: gainsboro;
      box-shadow: #382F2A 0 0 0 2px;
    }
    8%{
      border-style: double;
      border-color: #382F2A;
      box-shadow: gainsboro 0 0 2px 4px;
    }
    10%{
      box-shadow: gainsboro 0 0 8px 10px;
    }
    12%{
      border-style: solid;
      border-color: gainsboro;
      box-shadow: #382F2A 0 0 0 2px;
    }
    14%{
      border-style: double;
      border-color: #382F2A;
      box-shadow: gainsboro 0 0 2px 4px;
    }
    16%{
      box-shadow: gainsboro 0 0 8px 10px;
    }
    18%{
      border-style: double;
      border-color: #382F2A;
      box-shadow: #382F2A 0 0 0 8px;
    }
  }

  @keyframes radiate{
    4%{
      border-style: double;
      border-color: gainsboro;
      box-shadow: #382F2A 0 0 0 4px;
    }
    6%{
      border-style: solid;
      border-color: gainsboro;
      box-shadow: #382F2A 0 0 0 2px;
    }
    8%{
      border-style: double;
      border-color: #382F2A;
      box-shadow: gainsboro 0 0 2px 4px;
    }
    10%{
      box-shadow: gainsboro 0 0 8px 10px;
    }
    12%{
      border-style: solid;
      border-color: gainsboro;
      box-shadow: #382F2A 0 0 0 2px;
    }
    14%{
      border-style: double;
      border-color: #382F2A;
      box-shadow: gainsboro 0 0 2px 4px;
    }
    16%{
      box-shadow: gainsboro 0 0 8px 10px;
    }
    18%{
      border-style: double;
      border-color: #382F2A;
      box-shadow: #382F2A 0 0 0 8px;
    }
  }
}
.coming-soon-container {
  text-align: center;
}

.devclub-logo {
  height: 40vmin;
  pointer-events: none;
}

.coming-soon-content {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.devchamps-background {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    background-image: url(/static/media/devchamps-bg.288b713c.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: grid;
    justify-items: center;
    align-items: center;
}

.devchamps-container {
    height: 100%;
    width: 45%;
    min-width: 400px;
    display: grid;
    grid-template-rows: 15vw 1fr auto;
    border-color: #EFE7D8;
    border-width: 1em;
    border-left-style: solid;
    border-right-style: solid;
}

.devchamps-heading {
    background-color: #B34744;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 30% 70%;
}

.ashu {
    margin: 1.5em;
    background-image: url(/static/media/ashu-cool.e688a890.gif);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.title-grid{
    display: grid;
    grid-template-rows: 50% 50%;
}

.title {
    height: 100%;
    width: 100%;
    background-image: url(/static/media/devchamps-heading.6fb618f6.png);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.devchamps-container > .devchamps-heading > .devchamps-ashu {
    min-width: 0;
    min-height: 0;
    height: 100%;
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
}

.faq{
    text-align: center;
    color: #EFE7D8;
    font-family: 'Lilita One', cursive;
    font-size: 5em;
}

.faq-container {
    background-color: #EFE7D8;
    font-family: 'Montserrat';
    display: grid;
    justify-items: center;
    align-items: center;
}

.accordions{
    margin: 1em;
}

.devchamps-container > .faq-container .row-title-text {
    margin: 5px;
    margin-left: 20px;
    font-weight: 900;
    color: #5F394B !important;
}

.devchamps-container > .faq-container .row-title-text:active {
    color: #DB8263 !important;
}

.devchamps-container > .faq-container .row-content-text {
    margin-left: 15px;
    margin-right: 15px;
    line-height: 1.5rem;
    text-align: left;
}

.devchamps-container > .faq-container .row-content-text em {
    font-weight: 800;
}

.bottom{
    background-color: #799AAD;
    height: 100%;
    width: 100%;
}

.hof-coming-soon {
    font-family: 'Bebas Neue';
    color: #EFE7D8;
    font-size: 3.5rem;
    text-align: center;
}

@media screen and (max-width: 820px) {
    .devchamps-container {
        grid-template-rows: 25vw 1fr auto;
    }
    
    .hof-coming-soon {
        font-size: 2rem;
    }
}

@media screen and (max-width: 420px) {
    .devchamps-container {
        grid-template-rows: 32vw 1fr auto;
        border-width: 0px;
    }

    .ashu {
        margin: 0.5em;
    }

    .faq{
        font-size: 2.3em;
    }

    .accordions{
        margin: 0.4em 1em 0.4em 1em;
    }

    .hof-coming-soon {
        font-size: 1rem;
    }
}
